html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
  display: block;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: Open Sans;
  src: url("OpenSans.edf9e01b.ttf");
}

@font-face {
  font-family: Raleway;
  src: url("Raleway.8259b5d9.ttf");
}

header {
  background-color: #000;
}

.header__burger {
  display: none;
}

.header__wrap {
  max-width: 1510px;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.header__wrap-links {
  display: flex;
}

.header__wrap-links > a {
  text-align: center;
  height: 75px;
  max-width: 113px;
  color: #fff;
  justify-content: center;
  align-items: center;
  padding: 0 28px;
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 100;
  transition: all .3s;
  display: flex;
}

.header__wrap-links > a:hover {
  background: #ec7979;
}

.header__wrap-links > a:active {
  background: #ab4949;
}

.banner {
  max-height: 952px;
  max-width: 1510px;
  z-index: -1;
  background-color: #0000;
  background-image: linear-gradient(#00000080, #00000080), url("background_header.3b770a96.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 185px;
  left: auto;
  right: auto;
}

.banner__wrapper {
  justify-content: space-between;
  margin-top: 208px;
  display: flex;
}

.banner__wrapper .banner__info {
  flex-direction: column;
  display: flex;
}

.banner__wrapper .banner__info :nth-child(1) {
  height: 100px;
  width: 270px;
  margin-bottom: 51px;
  display: block;
}

.banner__wrapper .banner__info :nth-child(2) {
  max-width: 670px;
  color: #fff;
  display: block;
}

.banner__wrapper .banner__info-separator {
  width: 170px;
  border: 1px solid #fff;
  margin-top: 55px;
  margin-bottom: 42px;
}

.banner__wrapper .banner__info :nth-child(4) {
  max-width: 270px;
  color: #fff;
  margin-bottom: 42px;
}

.download__button {
  width: 270px;
  height: 58px;
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: 2px solid #fff;
  font-family: Open Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 100;
  transition: all .5s;
}

.download__button:hover {
  color: #ec7979;
  border: 2px solid #ec7979;
}

.download__button:active {
  color: #fff;
  background: #ab4949;
  border: 2px solid #ab4949;
}

.banner__form {
  max-width: 470px;
  min-width: 280px;
  width: 100%;
  max-height: 450px;
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.banner__form > h4 {
  min-width: 320px;
  color: #333;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 50px;
}

.banner__form > h4 > span {
  color: #ec7979;
}

.banner__form .name, .banner__form .email, .banner__form .password {
  max-width: 370px;
  width: 90%;
  border-style: hidden hidden solid;
  border-bottom-width: 1px;
  border-bottom-color: #bdbdbd;
  margin-bottom: 33px;
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

.btn-get-started {
  max-width: 470px;
  width: 100%;
  height: 60px;
  color: #fff;
  cursor: pointer;
  background: #ec7979;
  border: none;
  border-radius: 0 0 10px 10px;
  margin-top: 75px;
  transition: all .5s;
  position: relative;
}

.btn-get-started:hover {
  background: #ab4949;
}

.btn-get-started:active {
  background: #ec7979;
}

.form__get-started {
  max-width: 470px;
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.form__get-started ::placeholder {
  color: #bdbdbd;
}

.form__get-started input:focus, .form__get-started input:hover {
  border: 1px solid #c6cbcd;
  border-radius: 5px;
  outline: none;
  padding: 5px;
}

input {
  padding: 5px;
}

.social {
  background: #f8efef;
  padding-top: 25px;
  padding-bottom: 18px;
}

.social__wrapper-block {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.social__wrapper-text {
  flex-direction: column;
  display: flex;
}

.social__wrapper-text :nth-child(1) {
  color: #333;
  margin-bottom: 40px;
}

.social__wrapper-text :nth-child(2) {
  max-width: 470px;
  color: #86878b;
}

.social__nav {
  width: 650px;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.social__nav svg {
  max-width: 53px;
  width: 100%;
  max-height: 53px;
  height: 100%;
}

.social__nav svg:hover path {
  fill: #ab4949;
  transition: all .4s;
}

.social__nav svg:active path {
  fill: #ec7979;
  transition: all .4s;
}

.tabs-button__wrapper {
  flex-direction: row;
  display: flex;
}

.tab-btn:nth-child(1) {
  position: relative;
  top: 1px;
}

.tab-btn {
  max-width: 203px;
  width: 100%;
  height: 40px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #c6cbcd;
  border-bottom: hidden;
  border-radius: 5px 5px 0 0;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.tab-btn:hover {
  filter: drop-shadow(0 1px 3px #3333);
}

.tab-btn:active {
  background-color: #f5f5f7;
}

.tabs__wrapper-text {
  max-width: 700px;
  max-height: 100%;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 30px;
  display: flex;
}

.tabs__wrapper-text > h3 {
  max-width: 570px;
  max-height: 124px;
  color: #000;
  margin-top: 60px;
  margin-bottom: 60px;
  display: block;
}

.tabs__wrapper-text > h5 {
  max-width: 570px;
  margin-bottom: 27px;
  display: block;
}

.tabs-pic {
  max-height: 500px;
  width: 470px;
  background-image: url("tabs-pic.be05c80d.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
}

.tabs__wrapper {
  border: 1px solid #c6cbcd;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.download__button-tabs {
  max-width: 270px;
  height: 58px;
  color: #fff;
  cursor: pointer;
  transform: all .5s;
  background: #ec7979;
  border: 2px solid #fff;
  margin-bottom: 50px;
  font-family: Open Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 100;
}

.download__button-tabs:hover {
  background: #ab4949;
}

.download__button-tabs:active {
  background: #ec7979;
  box-shadow: inset 0 0 10px #ab4949;
}

.about__grid {
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 70px;
  display: grid;
}

.about__grid-block {
  text-align: center;
  max-width: 370px;
  height: 383px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.about__grid-block img {
  width: 170px;
  height: 170px;
}

.about__grid-block h5 {
  text-align: center;
}

section.pricing {
  background: #f5f5f5;
  padding: 100px 0 50px;
}

.pricing__grid {
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 80px;
  display: grid;
}

.pricing__grid-block {
  max-width: 370px;
  grid-template-columns: 1fr;
  grid-auto-flow: row;
  transition: all .3s;
  display: grid;
}

.pricing__grid-block:hover {
  filter: drop-shadow(0 4px 10px #3333);
  transform: translate(0, -20px);
}

.pricing__grid-block :nth-child(1) {
  height: 60px;
  color: #fff;
  text-transform: none;
  background-color: #333;
  border: 2px solid #f5f5f5;
  justify-content: center;
  display: flex;
}

.pricing__grid-block :nth-child(2) {
  height: 100px;
  color: #fff;
  background-color: #333;
  border: 2px solid #f5f5f5;
  justify-content: center;
  display: flex;
}

.pricing .pricing__grid-block :nth-child(3) {
  height: 50px;
  color: #fff;
  background-color: #333;
  border: 2px solid #f5f5f5;
  justify-content: center;
  display: flex;
}

.pricing__grid-block :nth-child(n+4):nth-child(-n+9) {
  height: 50px;
  color: #86878b;
  background: #fff;
  border: 2px solid #f5f5f5;
  justify-content: center;
  display: flex;
}

.reviews__grid {
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 80px;
  display: grid;
}

.reviews__grid-block {
  max-width: 370px;
  grid-template-rows: 1fr 80px;
  grid-template-columns: 1fr;
  margin-right: 20px;
  transition: all .3s;
  display: grid;
}

.reviews__arrow-wrapper > h5 {
  color: #86878b;
  text-indent: 10px;
  text-align: center;
  background-color: #e2e2e2;
  border-radius: 10px;
  justify-content: center;
  padding: 38px 20px;
  display: flex;
}

.reviews__arrow-wrapper {
  margin-bottom: 46px;
  transition: all .3s;
}

.reviews__arrow-wrapper:hover {
  transform: translate(0, -10px)scale3d(1.02, 1.02, 1);
}

.reviews__flex-wrapper {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.manager__wrapper {
  flex-direction: column;
  margin-left: 30px;
  display: flex;
}

.manager__wrapper > h4 {
  margin-bottom: 20px;
}

.review__pic-modifier {
  width: 70px;
  height: 70px;
}

.arrow-down {
  width: 0;
  height: 0;
  border-top: 20px solid #e2e2e2;
  border-left: 20px solid #0000;
  border-right: 20px solid #0000;
  margin-left: 50px;
}

section.banner2 {
  max-height: 442px;
  max-width: 1510px;
  z-index: -1;
  background-color: #0000;
  background-image: linear-gradient(#00000080, #00000080), url("background_banner2.20f783fa.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  margin-left: auto;
  margin-right: auto;
  left: auto;
  right: auto;
}

section.banner2 button {
  margin-top: 73px;
}

:root .banner2 h2, :root .banner2 h5 {
  color: #fff;
  text-align: center;
}

.banner__form-contact {
  max-width: 1000px;
  width: 100%;
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 90px;
  display: flex;
}

.name__contact, .email__contact, .password__contact {
  max-width: 571px;
  width: 90%;
  border-top-style: hidden;
  border-left-style: hidden;
  border-right-style: hidden;
  margin-bottom: 33px;
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

.password__contact {
  margin-bottom: 0;
}

.message__contact {
  max-width: 571px;
  width: 90%;
  border-style: hidden hidden solid;
  border-bottom-width: 1px;
  border-bottom-color: #bdbdbd;
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

.btn-send-message {
  max-width: 370px;
  width: 100%;
  height: 60px;
  color: #fff;
  cursor: pointer;
  background: #ec7979;
  border: none;
  margin-top: 50px;
  transition: all .5s;
  position: relative;
}

.btn-send-message:hover {
  background: #ab4949;
}

.btn-send-message:active {
  background: #ec7979;
}

.form__send-message {
  max-width: 1000px;
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.form__send-message ::placeholder {
  color: #bdbdbd;
}

.form__send-message input {
  padding: 5px;
}

.form__send-message input:focus, .form__send-message input:hover {
  border: 1px solid #c6cbcd;
  border-radius: 5px;
  outline: none;
  padding: 5px;
}

.message__contact input:focus, .message__contact input:hover {
  border: 1px solid #c6cbcd;
  border-radius: 5px;
  outline: none;
}

.form__send-message-wrapper1 {
  max-width: 1000px;
  width: 100%;
  flex-direction: row;
  display: flex;
}

.form__send-message-wrapper2 {
  max-width: 571px;
  width: 100%;
  flex-direction: column;
  margin-right: 20px;
  display: flex;
}

footer {
  background-color: #000;
}

footer h5 {
  color: #ffffff80;
  justify-content: center;
  margin-top: 34px;
  display: flex;
}

footer img {
  width: 53px;
  height: 53px;
}

.footer__container {
  max-width: 600px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  padding: 42px 0 27px;
  display: block;
  overflow: hidden;
}

.footer__nav svg {
  max-width: 53px;
  width: 100%;
  max-height: 53px;
  height: 100%;
}

.footer__nav svg:hover path {
  fill: #ab4949;
  transition: all .4s;
}

.footer__nav svg:active path {
  fill: #ec7979;
  transition: all .4s;
}

.footer__nav {
  max-width: 650px;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

@media screen and (max-width: 1280px) {
  :root main h1 {
    font-size: 36px;
  }

  :root main h2, :root main h3 {
    font-size: 24px;
  }

  :root main h4 {
    font-size: 18px;
  }

  :root main h5 {
    font-size: 14px;
  }

  .banner__info {
    max-width: 500px;
    margin-right: 50px;
  }

  :root .banner__info {
    max-width: 450px;
    margin-left: 50px;
  }

  :root .banner__wrapper {
    margin-top: 137px;
  }

  :root .banner__info :nth-child(1) {
    margin-bottom: 15px;
  }

  :root .banner__info :nth-child(4) {
    margin-bottom: 20px;
  }

  :root .banner__info-separator {
    margin-top: 25px;
    margin-bottom: 25px;
  }

  :root social {
    padding: 10px 0;
  }

  :root .social__wrapper-text :nth-child(1) {
    margin-bottom: 20px;
  }

  :root .tabs-button__wrapper {
    margin-top: 60px;
  }

  :root .about__grid {
    margin-top: 22px;
  }

  :root .about__grid-block {
    height: 280px;
  }

  :root .about__grid-block img {
    width: 147px;
    height: 147px;
  }

  :root section:not(section.banner):not(section.social) {
    padding: 70px 0 50px;
  }

  :root .section__separator {
    margin-top: 25px;
    margin-bottom: 25px;
  }

  :root .download__button {
    margin-top: 33px;
  }

  :root .banner__form-contact {
    margin-top: 55px;
  }
}

@media screen and (max-width: 767px) {
  :root .header__wrap-links, :root .header__wrap-logo {
    display: none;
  }

  :root .header__burger {
    max-width: 1510px;
    width: 90%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  :root .header__burger:hover path {
    fill: #ab4949;
    transition: all .4s;
  }

  :root .header__burger:active path {
    fill: #ec7979;
    transition: all .4s;
  }

  :root .banner__wrapper {
    flex-direction: column;
    align-items: center;
    margin-top: 84px;
  }

  :root section.banner {
    padding-bottom: 50px;
  }

  :root section.banner .banner__info {
    text-align: center;
    max-width: 500px;
    align-items: center;
    margin-bottom: 56px;
  }

  :root section.banner .banner__info > img {
    display: none;
  }

  :root .social__wrapper-block {
    text-align: center;
    flex-direction: column;
  }

  :root .social__wrapper-block .social__wrapper-text {
    align-items: center;
  }

  :root .social__wrapper-block .social__nav {
    max-width: 650px;
    width: 100%;
    margin-top: 32px;
  }

  :root .tabs-pic {
    display: none;
  }

  :root .tabs-button__wrapper {
    margin-top: 0;
  }

  :root section:not(section.banner):not(section.social) {
    padding: 50px 0 40px;
  }

  :root .about__grid, :root .pricing__grid {
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr;
    justify-items: center;
  }

  :root .pricing__grid-block {
    max-width: 720px;
    width: 100%;
    margin-bottom: 27px;
  }

  :root .reviews__grid {
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr;
    justify-items: center;
  }

  :root .reviews__grid-block {
    margin-bottom: 27px;
  }

  :root .form__send-message input {
    width: 100%;
  }

  :root .form__send-message-wrapper1 {
    flex-direction: column;
  }

  :root .message__contact {
    height: 150px;
    max-width: 571px;
    width: 100%;
  }

  :root .message__contact::placeholder {
    padding-top: 50px;
  }

  :root .section__text-wrapper {
    padding: 0 25px;
  }

  :root footer a {
    padding: 0 5px;
  }
}

@media (min-width: 300px) and (max-width: 1200px) {
  :root h1 {
    font-size: calc(1.33333vw + 32px);
  }

  :root h2 {
    font-size: calc(1.33333vw + 20px);
  }

  :root h3 {
    font-size: calc(.666667vw + 22px);
  }

  :root h4 {
    font-size: calc(.666667vw + 16px);
  }

  :root h5 {
    font-size: calc(.444444vw + 12.6667px);
  }
}

html {
  scroll-behavior: smooth;
}

h1 {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  font-family: Raleway;
  font-size: 48px;
  font-style: normal;
  font-weight: 500;
  display: flex;
}

h2 {
  color: #000;
  align-items: center;
  font-family: Raleway;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  display: flex;
}

h3 {
  color: #000;
  align-items: center;
  font-family: Open Sans;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  display: flex;
}

h4 {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  font-family: Open Sans;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  display: flex;
}

h5 {
  color: #000;
  align-items: center;
  font-family: Raleway;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  display: flex;
}

h6 {
  color: #000;
  align-items: center;
  font-family: Open Sans;
  font-size: 13px;
  font-style: normal;
  font-weight: 700;
  display: flex;
}

section:not(section.banner):not(section.social) {
  padding: 100px 0 60px;
}

.container {
  max-width: 1170px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
  display: block;
  overflow: hidden;
}

.section__text-wrapper {
  text-transform: uppercase;
  flex-direction: column;
  align-items: center;
  display: flex;
